<template>
    <div class="max-w-full bg-gray-200 p-4 rounded">
        <div class="max-w-6xl mx-auto bg-white p-2"> 
            <div class="w-full bg-green-500 p-2 rounded-md">
                <label for="" class="text-2xl text-white">Appointment List</label>
            </div>
            <div class="flex justify-between items-center">
                <div class="text-left py-2">
                    <router-link :to="{ name:'adminAppointmentCreate', params: { schedule_id : scheduleId }}" class="bg-blue-500 text-white rounded-md p-2">Create Appointment</router-link>
                </div>
                <div>
                     <span class="text-white p-2 bg-blue-500 rounded-md cursor-pointer" @click="historyModel =! historyModel"> Requested Mentor </span>
                </div>
            </div>
            <div>
                <!-- {{ appointments.expected_mentors_frequescy }} -->
               <div>
                    <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="historyModel ">
                        <div class="relative mx-auto w-auto max-w-xl">
                            <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                                <div class="modal-body relative p-4 text-lg">
                                    <div class="text-left p-4">
                                        <div>
                                            <table class="w-full table-auto">
                                                <thead class="bg-white border-b">
                                                    <tr>
                                                        <th scope="col" class="text-md font-medium text-gray-900 px-6 py-4 text-left">Mentor Name</th>
                                                        <th scope="col" class="text-md font-medium text-gray-900 px-6 py-4 text-left"> Phone </th>
                                                        <th scope="col" class="text-md font-medium text-gray-900 px-6 py-4 text-left"> Frequency </th>
                                                        <th scope="col" class="text-md font-medium text-gray-900 px-6 py-4 text-left" >Mentor Details</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr class="border-b" v-for="mentorRequest in appointments.expected_mentors_frequescy" :key="mentorRequest.id">
                                                        <td class="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{{ mentorRequest.name }}</td>
                                                        <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">{{ mentorRequest.phone }}</td>
                                                        <td class="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">{{ mentorRequest.frequency }}</td>
                                                        <td class="text-sm text-gray-900 text-center font-light px-6 py-4 whitespace-nowrap">
                                                            <button @click="mentorDetails =! mentorDetails, mentorFind(mentorRequest.id)" class="p-2 bg-green-300 rounded-md">
                                                                 Details 
                                                            </button>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                            <div>
                                                <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="mentorDetails ">
                                                    <div class="relative mx-auto w-auto max-w-xl">
                                                        <div class="bg-white w-full rounded shadow-2xl flex flex-col">
                                                            <div class="modal-body relative p-4 text-lg">
                                                                <div class="text-left p-4">
                                                                    <p>Name : {{ mentor_details.name }}</p>
                                                                    <p>Phone: {{ mentor_details.phone }}</p>
                                                                    <p>Email: {{ mentor_details.email }}</p>
                                                                    <p>Gender: {{ mentor_details.gender == 1 ? 'Male':'Female' }}</p>
                                                                    <p>BMDC: {{ mentor_details.bmdc }}</p>
                                                                    <p>Medical College: {{ mentor_details.medical }}</p>
                                                                </div>
                                                            </div>
                                                            <div class="modal-footer text-center p-2 border-t border-gray-200 rounded-b-md">
                                                                <button type="button"
                                                                    class="inline-block px-6 py-2 bg-brand text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out"
                                                                    @click="mentorDetails=false">
                                                                    Close
                                                                </button>

                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="mentorDetails"></div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="modal-footer text-center p-2 border-gray-200 rounded-b-md">
                                    <Btn type="button" @click="historyModel=false">
                                        Close
                                    </Btn>

                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="absolute inset-0 z-40 opacity-25 bg-gray-400" v-if="historyModel"></div>
                </div>
            </div>
            <!-- {{ appointments.type }} -->
            <table class="w-full mx-auto">
                <thead class="border">
                    <tr>
                        <th class="border-r p-2">SL</th>
                        <th class="border-r p-2">Schedule</th>
                        <!-- <th class="border-r p-2">User</th> -->
                        <th class="border-r p-2">Patient Name</th>
                        <th class="border-r p-2">Serial</th>
                        <th class="border-r p-2">Type</th>
                        <th class="border-r b-2">Question</th>
                        <th class="border-r p-2">Payable</th>
                        <th class="border-r p-2">Requested Mentor</th>
                        <th class="border-r p-2">Mentor Assign</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody class="border">
                    <tr v-for="(appointment, index) in appointments.data" :key="index" class="hover:bg-gray-200">
                        <td class="border-r border-b p-2">{{ appointment.id }}</td>
                        <td class="border-r border-b p-2">{{ appointment.schedule_id }}</td>
                        <td class="border-r border-b p-2">{{ appointment.patientName}}</td>
                        <!-- <td class="border-r border-b p-2">{{ appointment.user_id }}</td> -->
                        <td class="border-r border-b p-2">{{ appointment.serial }}</td>
                        <td class="border-r border-b p-2" >{{ appointment.type }} </td>
                        <td class="border-r border-b p-2">{{ appointment.questions }}</td>
                        <td class="border-r border-b p-2">{{ appointment.payable }}</td>
                        <td class="border-r border-b p-2">{{ appointment.requested_mentor }}</td>
                        <td class="border-r border-b p-2">
                            <span>{{ appointment.assigned_mentor.name }}</span>
                            <div v-if="appointment.assigned_mentor">
                                <btn @click="mentorAssign = appointment.id">
                                     Edit 
                                </btn>

                                <div>
                                    <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="mentorAssign == appointment.id">
                                        <div class="relative mx-auto w-auto max-w-2xl">
                                            <div class="bg-white  rounded shadow-2xl flex flex-col">
                                                <div class="text-right text-red-600 rounded-b-md">
                                                        <button type="button" class="p-2" @click="mentorAssign=false" >&#10006;</button>
                                                </div>
                                                <div class="modal-body relative text-lg">
                                                    <div class="text-left p-2">
                                                        <label for="">Mentor</label>
                                                        <Select class="w-full border py-3 bg-white outline-none p-2" v-model="mentor_id">
                                                            <option value=""> Select User </option>
                                                            <option :value="user.id" v-for="user in appointments.mentors[appointment.type_id]" :key="user.id">
                                                                {{ user.name ? user.name:'' }}
                                                            </option>
                                                        </Select>
                                                    </div>
                                                </div>
                                                <div class="modal-footer text-center p-2 border-t border-gray-200 rounded-b-md">
                                                    <btn type="button" @click="mentorAssignUpdate(appointment.id, appointment.assigned_mentor_id)">
                                                        Submit
                                                    </btn> 

                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div v-else>
                                <button @click="mentorAssign = appointment.id"  class="p-2 bg-green-300 rounded-md">
                                     Assign 
                                </button>

                                <div>
                                    <div class="fixed overflow-x-hidden overflow-y-auto inset-0 flex justify-center items-center z-50" v-if="mentorAssign == appointment.id">
                                        <div class="relative mx-auto w-auto max-w-2xl">
                                            <div class="bg-white  rounded shadow-2xl flex flex-col">
                                                <div class="text-right text-red-600 rounded-b-md">
                                                        <button type="button" class="p-2" @click="mentorAssign=false" >&#10006;</button>
                                                </div>
                                                <div class="modal-body relative text-lg">
                                                    <div class="text-left p-2">
                                                        <label for="">Mentor</label>
                                                        <Select class="w-full border py-3 bg-white outline-none p-2" v-model="mentor_id">
                                                            <option value=""> Select User </option>
                                                            <option :value="user.id" v-for="user in  appointments.mentors[appointment.type_id]" :key="user.id">{{ user.name ? user.name:'' }}</option>
                                                        </Select>
                                                    </div>
                                                </div>
                                                <div class="modal-footer text-center p-2 border-t border-gray-200 rounded-b-md">
                                                    <button type="button"
                                                        class="inline-block px-6 py-2 bg-brand text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-purple-700 hover:shadow-lg focus:bg-purple-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-purple-800 active:shadow-lg transition duration-150 ease-in-out"
                                                        @click="mentorAssignSubmit(appointment.id)">
                                                        Submit
                                                    </button> 

                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </td>
                        <td class="border-b p-2">
                            <div class="flex flex-wrap gap-2 items-center justify-center">
                                <router-link :to="{ name: 'adminAppointmentEdit' , params:{ appointmentId:appointment.id }}" class="bg-blue-500 text-white text-sm p-2 rounded-md">Edit</router-link>
                                <router-link to="/admin/schedules" class="bg-red-500 text-white text-sm p-2 rounded-md">Delete</router-link>
                                <router-link :to="{ name: 'adminFeedbackIndex', params: { appointmentId: appointment.id }}" class="bg-blue-500 text-white text-sm p-2 rounded-md">
                                        Feedback
                                </router-link>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>

        </div>
    </div>
</template>

<script>
import Select from '../../../../components/select.vue'
import Input from '../../../../components/input-text.vue'
import Textarea from '../../../../components/input-text-area.vue'
import Btn from '../../../../components/btn.vue'
import axios from "../../../../store/axios"
export default {
    components:{
        Select,
        Input,
        Textarea,
        Btn
    },
    data(){
        return {
            historyModel:'',
            mentorDetails:'',
            mentor_details: '',
            mentorAssign: '',
            mentor_id: '',
        }
    },

    created(){
        this.$store.dispatch('adminAppointment/getAppointments', {
            scheduleId:this.scheduleId
        });
    },
    computed: {
        appointments(){
             return this.$store.state.adminAppointment.appointments;
        },
        scheduleId(){
            return this.$route.params.scheduleId;
        }
    },
    methods:{
        mentorFind(element){
           this.mentor_details = Object.values(this.appointments.expected_mentors_frequescy).find((obj) => {
                    return( obj.id == element)
                });
        },
        async mentorAssignSubmit(element){
            if(this.mentor_id && element){
                let mentorAssign = await axios().post('/api/mentor-assign', {
                    appointment_id: element,
                    mentor_id: this.mentor_id,
    
                }).then( () => { 
                     this.$router.back();
                } )
            }
            else {
                alert('Please Input Proper value')
            }
            console.log(mentorAssign);
        },
        async mentorAssignUpdate(element, id){
            // console.log(element, id)
            if(this.mentor_id && element){
                let mentorAssign = await axios().put(`/api/mentor-assign/${id}`, {
                    appointment_id: element,
                    mentor_id: this.mentor_id,
    
                }).then( () => { 
                     this.$router.back();
                } )
            }
            else {
                alert('Please Input Proper value')
            }
            // console.log(mentorAssign);
        },
    }
}
</script>